.app {
  font-family: sans-serif;
}

p {
  margin: 0;
}

.game-info {
  position: fixed;
  top: 78.5%;
  margin-left: 10px;

}

.tile-container {
  background: transparent;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.player-1-nonactive-container {
  background: transparent;
  position: fixed;
  top: 10px;
  left: 23%;

}

.player-2-nonactive-container {
  background: transparent;
  position: fixed;
  bottom: 10px;
  left: 23%;
}

.game-board {
  background: #cbcbcb;
  position: fixed;
  height: 70%;
  width: 100%;
  top: 43%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.tile {
 vertical-align: top;
 background: #fff;
 border: 2px solid #000;
 margin: -1px;
 font-size: 24px;
 width: 100px;
 height: 100px;
 padding: 0;
}

.tile-decal {
  height: 70px;
  width: 70px;
}

.tile-blank {
  vertical-align: top;
  background: transparent;
  border: 0px solid #000;
  margin: 1px;
  font-size: 24px;
  font-weight: bold;
  width: 96px;
  height: 96px;
  padding: 0;
}


